import React, { useState, useEffect } from "react";
import { Doughnut } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import axios from 'axios';

// Register chart.js components
ChartJS.register(ArcElement, Tooltip, Legend);

const App = () => {
  const [taskInput, setTaskInput] = useState("");
  const [tasks, setTasks] = useState([]);
  const [isNavOpen, setIsNavOpen] = useState(false); // Toggle state for side nav
  const [taskCategory, setTaskCategory] = useState("");
  const [taskPriority, setTaskPriority] = useState("");
  const [openCategory, setOpenCategory] = useState(null);
  const [categories, setCategories] = useState(["Work", "Personal", "Urgent"]);
  const [newCategory, setNewCategory] = useState("");
  const [selectedTheme, setSelectedTheme] = useState("light");

  const lightTheme = {
    background: "bg-white",
    text: "text-gray-800",
    navbar: "bg-pink-600 text-white",
    button: "bg-pink-600 text-white hover:bg-pink-700",
    card: "bg-white p-6 rounded-lg shadow-md space-y-6",
  };

  const darkTheme = {
    background: "bg-gray-800",
    text: "text-white",
    navbar: "bg-gray-900 text-white",
    button: "bg-purple-600 text-white hover:bg-purple-700",
    card: "bg-gray-700 p-6 rounded-lg shadow-md space-y-6",
  };

  // Select theme based on state
  const currentTheme = selectedTheme === "light" ? lightTheme : darkTheme;

  const handleInputChange = (e) => setTaskInput(e.target.value);

  const addTask = () => {
    const newTask = {
      id: tasks.length + 1,
      description: taskInput,
      category: taskCategory,
      priority: taskPriority,
    };
    setTasks([...tasks, newTask]);
    setTaskInput("");
    setTaskCategory("");
    setTaskPriority("");
  };

  const toggleCategory = (category) => {
    setOpenCategory(openCategory === category ? null : category);
  };

  const addCategory = () => {
    if (newCategory && !categories.includes(newCategory)) {
      setCategories([...categories, newCategory]);
      setNewCategory(""); // Clear input field after adding
    } else {
      alert("Please enter a valid category name.");
    }
  };

  useEffect(() => {
    const storedTasks = JSON.parse(localStorage.getItem('tasks')) || [];
    if (storedTasks.length === 0) {
      axios.get('http://localhost:5000/tasks')
        .then((response) => {
          setTasks(response.data);
          localStorage.setItem('tasks', JSON.stringify(response.data));
        })
        .catch((error) => console.error("Error fetching tasks:", error));
    } else {
      setTasks(storedTasks);
    }
  }, []);

  // Doughnut chart data
  const chartData = {
    labels: ["High Priority", "Medium Priority", "Low Priority"],
    datasets: [
      {
        data: [
          tasks.filter((task) => task.priority === "High").length,
          tasks.filter((task) => task.priority === "Medium").length,
          tasks.filter((task) => task.priority === "Low").length,
        ],
        backgroundColor: ["#FF6347", "#FFD700", "#32CD32"],
        hoverBackgroundColor: ["#FF4500", "#FFA500", "#228B22"],
      },
    ],
  };

  return (
    <div className={`${currentTheme.background} min-h-screen font-sans ${currentTheme.text}`}>
      {/* Navbar */}
      <nav className={`${currentTheme.navbar} p-4 flex justify-between items-center fixed top-0 left-0 right-0 z-20 shadow-md`}>
        <h1 className="text-2xl font-semibold">Task Manager</h1>
        <button
          className="lg:hidden text-white focus:outline-none"
          onClick={() => setIsNavOpen(!isNavOpen)} // Toggles side nav visibility
        >
          {/* Hamburger Icon */}
          <div className="w-6 h-0.5 bg-white mb-1"></div>
          <div className="w-6 h-0.5 bg-white mb-1"></div>
          <div className="w-6 h-0.5 bg-white"></div>
        </button>
        
      </nav>

      {/* Side Navigation */}
      <div
        className={`fixed top-0 left-0 w-64 h-full ${currentTheme.navbar} transition-all duration-300 transform ${isNavOpen ? "translate-x-0" : "-translate-x-full"} lg:translate-x-0 lg:block`}
      >
        <div className="p-6">
          <h2 className="text-xl font-semibold">Navigation</h2>
          <ul className="mt-4 space-y-4">
            <li><a href="#" className="text-lg hover:text-pink-200">Dashboard</a></li>
            <li><a href="#" className="text-lg hover:text-pink-200">Tasks</a></li>
            <li><a href="#" className="text-lg hover:text-pink-200">Settings</a></li>
            <li><a href="#" className="text-lg hover:text-pink-200"
          onClick={() => setSelectedTheme(selectedTheme === "light" ? "dark" : "light")}
        >
          Toggle Theme
        </a></li>
            <li><a href="#" className="text-lg hover:text-pink-200">Profile</a></li>
          </ul>
        </div>
      </div>

      {/* Main Content */}
      <div className="lg:ml-64 p-6 pt-10 space-y-8 overflow-y-auto">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Doughnut Chart */}
          <div className={`${currentTheme.card} mt-10`}>
            <h2 className="text-xl font-semibold text-pink-600 mb-4">Task Priority Distribution</h2>
            <div className="w-full aspect-w-1 aspect-h-1 max-h-96">
              <Doughnut data={chartData} options={{
                responsive: true,
                maintainAspectRatio: true,
                plugins: {
                  legend: {
                    position: 'right', // Move legend to the right
                    labels: {
                      boxWidth: 15, // Adjust the width of the legend box
                      padding: 20, // Adjust the spacing between legend items
                    },
                  },
                },
              }} height={250} />
            </div>
          </div>

          {/* Add Task Section */}
          <div className={`${currentTheme.card} space-y-12 mt-10`}>
            <h2 className="text-xl font-semibold text-pink-600 mt-2">Add a New Task</h2>
            <div>
              <input
                type="text"
                value={taskInput}
                onChange={handleInputChange}
                placeholder="Enter task description"
                className="px-4 py-2 w-full border border-pink-500 rounded-md mb-2"
              />
            </div>
            <div className="flex space-x-4">
              <select
                value={taskCategory}
                onChange={(e) => setTaskCategory(e.target.value)}
                className="px-4 py-2 w-1/2 border border-pink-500 rounded-md text-black"
              >
                <option value="">Select Category</option>
                {categories.map((category) => (
                  <option key={category} value={category}>{category}</option>
                ))}
              </select>

              <select
                value={taskPriority}
                onChange={(e) => setTaskPriority(e.target.value)}
                className="px-4 py-2 w-1/2 border border-pink-500 rounded-md text-black"
              >
                <option value="">Select Priority</option>
                <option value="High">High</option>
                <option value="Medium">Medium</option>
                <option value="Low">Low</option>
              </select>
            </div>
            <button
              onClick={addTask}
              className={`${currentTheme.button} w-full px-4 py-2 mt-4`}
            >
              Add Task
            </button>
          </div>
        </div>

        {/* Add New Category */}
        <div className="mb-6">
          <h3 className="text-lg font-semibold text-pink-600 mb-2">Add a New Category</h3>
          <div className="flex space-x-4">
            <input
              type="text"
              value={newCategory}
              onChange={(e) => setNewCategory(e.target.value)}
              placeholder="Enter new category"
              className="px-4 py-2 border border-pink-500 rounded-md w-full"
            />
            <button
              onClick={addCategory}
              className={`${currentTheme.button} px-4 py-2 rounded-md`}
            >
              Add
            </button>
          </div>
        </div>

        {/* Task List with Accordion */}
        <div className={`${currentTheme.card}`}>
          {categories.map((category) => (
            <div key={category}>
              <div
                className="flex justify-between items-center cursor-pointer"
                onClick={() => toggleCategory(category)} // Toggle on click
              >
                <h3 className="text-lg font-semibold text-pink-600">{category}</h3>
                <span className="text-xl">{openCategory === category ? "-" : "+"}</span> {/* Show + or - */}
              </div>

              {/* Task List */}
              <div
                className={`space-y-4 transition-all duration-300 ease-in-out ${openCategory === category ? "max-h-screen" : "max-h-0 overflow-hidden"}`}
                style={{ transitionProperty: "max-height" }} // Smooth collapse/expand effect
              >
                {tasks
                  .filter((task) => task.category === category)
                  .map((task) => (
                    <div
                      key={task.id}
                      className="p-4 bg-pink-50 border border-pink-200 rounded-md shadow-sm"
                    >
                      <p className="text-gray-800">{task.description}</p>
                      <p className="text-sm text-gray-500">{task.priority} Priority</p>
                    </div>
                  ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default App;
